h1 a.highlighter, h2 a.highlighter, h3 a.highlighter, h4 a.highlighter, h5 a.highlighter, h5 a.highlighter {
  text-decoration: none;
  border-bottom: 1px solid rgba(56, 116, 168, 0.3);
  box-shadow: 0 -4px 0 rgba(56, 116, 168, 0.3) inset;
  transition: border-bottom .25s, box-shadow .25s; }
  h1 a.highlighter:hover, h2 a.highlighter:hover, h3 a.highlighter:hover, h4 a.highlighter:hover, h5 a.highlighter:hover, h5 a.highlighter:hover {
    text-decoration: none;
    border-bottom: 1px solid rgba(56, 116, 168, 0.8);
    box-shadow: 0 -4px 0 rgba(56, 116, 168, 0.8) inset; }

p a.highlighter {
  text-decoration: none;
  border-bottom: 1px solid rgba(56, 116, 168, 0.3);
  box-shadow: 0 -2px 0 rgba(56, 116, 168, 0.3) inset;
  transition: border-bottom .25s, box-shadow .25s; }
  p a.highlighter:hover {
    text-decoration: none;
    border-bottom: 1px solid rgba(56, 116, 168, 0.8);
    box-shadow: 0 -2px 0 rgba(56, 116, 168, 0.8) inset; }

.container .footerclass {
  background: url(images/blue-ridge-footer-ltx1800.png) no-repeat center bottom;
  min-height: 300px; }

.sidebar-footer-logos {
  padding-top: 2em; }

.widget [class*="wp-image"] {
  margin-left: 0;
  margin-right: 0; }

.widget h3 {
  text-decoration: none;
  border-bottom: 1px solid rgba(56, 116, 168, 0.3);
  box-shadow: 0 -2px 0 rgba(56, 116, 168, 0.3) inset;
  transition: border-bottom .25s, box-shadow .25s;
  font-size: 1.6rem;
  line-height: 1.2; }
  .widget h3 a:hover {
    text-decoration: none;
    border-bottom: 1px solid rgba(56, 116, 168, 0.8);
    box-shadow: 0 -2px 0 rgba(56, 116, 168, 0.8) inset; }

.cover-image .text-overlay .subtitle {
  font-size: 1.4rem;
  line-height: 1.4; }

.rwd-break-600 {
  display: none; }

.arc-bright-spots #cdt .cdt-chart {
  max-width: 100%;
  padding-right: 0;
  padding-left: 0; }

@media screen and (min-width: 600px), print {
  .rwd-break-600 {
    display: inline; }
  .cover-image {
    position: relative; }
    .cover-image .text-overlay {
      position: absolute;
      bottom: 15%;
      left: 1.8em;
      right: 1.8em; }
      .cover-image .text-overlay .main-title {
        color: #ffffff;
        font-size: 3.6rem;
        line-height: 4.2rem;
        text-shadow: 1px 1px 8px rgba(0, 0, 0, 0.75); }
      .cover-image .text-overlay .subtitle {
        color: #ffffff;
        text-shadow: 1px 1px 6px rgba(0, 0, 0, 0.75);
        font-size: 1.4rem; } }
